import React from 'react';
import { GoogleLogin } from '@react-oauth/google';
import './Login.css';
import {
  Redirect,
} from 'react-router-dom';
import jwt_decode from 'jwt-decode';

function Login({ clientId, user, setUser }) {
  const onSuccess = (res) => {
    setUser(jwt_decode(res.credential));
    // decodeJwtResponse() is a custom function defined by you
    // to decode the credential response.
    // const responsePayload = jwt_decode(res.credential);

    // console.log("ID: " + responsePayload.sub);
    // console.log('Full Name: ' + responsePayload.name);
    // console.log('Given Name: ' + responsePayload.given_name);
    // console.log('Family Name: ' + responsePayload.family_name);
    // console.log("Image URL: " + responsePayload.picture);
    // console.log("Email: " + responsePayload.email);
  }

  const onFailure = (res) => {
    setUser(new Error('Failed to login'));
  }

  return (
    <div id='login'>
      <h1>
        <b>ECLAIR</b> Point Scanner
      </h1>
      <GoogleLogin
        clientId={clientId}
        onSuccess={onSuccess}
        onFailure={onFailure}
        size={'large'}
        text={'continue_with'}
        shape={'pill'}
        useOneTap={true}
        auto_select={true}
        // login_uri={'https://9f6d-47-184-4-166.ngrok.io/scan'}  // enabling this breaks the authentication for some reason
        context={'use'}
        ux_mode={'popup'}
        itp_support={true}
      />
      {user.sub ? <Redirect to="/" /> : <></>}
      <span id='login-fail'>{(user instanceof Error) ? user.message : ''}</span>
    </div>
  );
}

export default Login;
