import React, { useState } from 'react';
import { QrReader } from 'react-qr-reader';
import { googleLogout } from '@react-oauth/google';
import { Link } from 'react-router-dom';
// import axios from 'axios';
import SteinStore from 'stein-js-client';
import './Scan.css';
import {
  Redirect,
} from "react-router-dom";

const ENDPOINT = process.env.REACT_APP_SHEETS_ENDPOINT;
const REDEEMED = {}
const store = new SteinStore(ENDPOINT);


function Scan({ user, setUser }) {
  const [ scanMessage, setScanMessage ] = useState(<></>);

  return (
    <div id='scanner'>
      <p>
        Signed in as <a href={'https://docs.google.com/spreadsheets/d/1LjpWtun7Sm_bnqSDu3MgKJy1a27swn9eHpibRHh3GDg/edit?usp=sharing'}>{user.name}</a>.&nbsp;
        <Link
          to='/login'
          onClick={() => {
            googleLogout();
            setUser({});
          }}
        >
          Not you?
        </Link>
      </p>
      <QrReader
        constraints={
          {
            facingMode: 'environment'
          }
        }
        onResult={(result, error) => {
          if (!!result) {
            const scanned = result?.text;
            const [ code, task, points ] = scanned.match(/(.+):([0-9]+)/i);
            if (!code) {
              return;
            }

            if (REDEEMED[task] === points) {  // if this has already been redeemed
              return;
            }

            setScanMessage(<span>Submitting points...</span>);
            REDEEMED[task] = points;
            store
              .edit('main', {
                search: {
                  ID: user.sub,
                },
                set: {
                  [task]: parseInt(points),
                },
              }).then((res) => {
                REDEEMED[task] = points;
                if (res.totalUpdatedRows === 0) {  // the object was not updated cuz it doesnt exist
                  console.log(res);
                  store
                    .append('main', [
                      {
                        ID: user.sub,
                        Name: user.name,
                        // 'Total Points': '=SUM(INDIRECT("R[0]C[1]",FALSE):INDIRECT("R[0]C[1000]",FALSE))',
                        [task]: parseInt(points),
                      }
                    ])
                    .then((res) => {
                      console.log(res);
                    })
                }
              }).then((res) => {
                setScanMessage(<span>Redeemed {task} for <b>{points}</b> points!</span>);
              });
          }

          if (!!error) {
            // console.info(error);
          }
        }}
        className={'camera'}
      />
      {user.sub ? <></> : <Redirect to="/login" />}
      {scanMessage}
    </div>
  );
}

export default Scan;
