import React, { useState } from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";
import Login from './Login.js';
import Scan from './Scan.js';
import { GoogleOAuthProvider } from '@react-oauth/google';

const clientId = '859188986524-f1uh3i09qcmvvgltt4cik70l50bcj2ao.apps.googleusercontent.com'

function App(props) {
  const [user, setUser] = useState({});

  return (
    <GoogleOAuthProvider clientId={clientId}>
      <Router basename={'/scan'}>
        <Switch>
          <Route exact path="/">
            <Scan
              user={user}
              setUser={setUser}
            />
          </Route>
          <Route path="/login">
            <Login
              clientId={clientId}
              user={user}
              setUser={setUser}
            />
          </Route>
        </Switch>
      </Router>
    </GoogleOAuthProvider>
  );
}

export default App;
